#information-box{display:flex;margin:auto}#information{margin:auto;height:20px;width:80%}.card{animation-name:drawCard;animation-duration:1.5s}.playCard{animation-name:playCard;animation-duration:2s}.discardCard{animation-name:discardCard;animation-duration:3s}.selectCard{animation-name:selectCard;animation-duration:1s;filter:brightness(25%)}@keyframes drawCard{0%{opacity:0;transform:translateX(350px)}20%{opacity:0.8}100%{opactiy:1;transform:translate(0)}}@keyframes playCard{0%{opacity:1}50%{opacity:1;transform:translateY(-150px)}100%{opacity:0;transform:translate(250px, 250px)}}@keyframes discardCard{0%{opacity:1}10%{opacity:0}100%{opacity:0}}@keyframes selectCard{100%{filter:brightness(25%)}}#dash-box{position:absolute;width:100%;z-index:2;bottom:0px;display:flex;flex-direction:column}#dash-displays-box{display:flex;justify-content:space-evenly}#dash-bottom-box{display:flex;justify-content:space-evenly}#center-panel{width:800px}.bottom-terminal{width:320px;height:120px}#card-description{width:300px;height:100px;font-size:25px;background-color:rgba(117,250,117,0.1);color:white;padding:10px;position:relative}#card-description-span{padding:10px 20px;position:absolute}#enemy-display{padding:10px;width:300px;height:100px;background-color:rgba(255,0,0,0.1);font-size:25px;color:#ffbb00;position:relative}#enemy-display-span{padding:10px 20px;position:absolute}#end-turn-button{align-self:center;background-color:red;border:3px solid black;border-radius:50%;height:65px;width:65px}#game-controls{width:700px;display:flex;justify-content:space-between;margin-top:20px}#player-hand{width:80%}#player-deck{position:relative}#deck-size{font-size:30px;position:absolute;right:1px;bottom:90%;color:red}#right-panel{position:relative}#enemy-shields-num,#enemy-armor-num{padding:15px;font-size:25px;position:absolute;color:#6f6}.top-row{display:flex;margin:auto;justify-content:space-around}.visible{opacity:1;visibility:visible}.hidden{opacity:0;visibility:hidden}.disabled-btn{background-color:grey}.active-btn{background-color:red}.popup{height:100vh;width:100%;position:fixed;top:0;left:0;background-color:rgba(0,0,0,0.8);z-index:500;transition:all 0.2s}.popup-content{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);width:75%;background-color:white;height:50vh;box-shadow:0 2rem 4rem rgba(0,0,0,0.2);border-radius:2px;font-size:20px}#rules-pic-box{height:100%;width:100%;position:fixed;top:0;left:0;background-color:rgba(0,0,0,0.8);z-index:500;transition:all 0.2s}#rules-pic{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);background-color:white;box-shadow:0 2rem 4rem rgba(0,0,0,0.2);border-radius:2px;font-size:20px}.start-screen-div{position:absolute;top:100px;right:280px;color:white;width:400px;height:500px;font-size:30px}#top-panel{margin-left:100px;position:absolute;height:100px;width:460px;display:flex;justify-content:space-evenly}.status-display{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.top-terminal{top:5px;position:relative}#player-lasers-num{top:10px;right:310px;font-size:50px;color:#6f6;font-size:40px;position:absolute}#player-missles-num{top:10px;right:120px;font-size:50px;color:#6f6;font-size:40px;position:absolute}#popup-top-row{display:flex;flex-direction:row-reverse;justify-content:space-between}#rules-div{width:600px}*{font-family:"VT323"}#game-box{position:absolute;height:800px}#game-screen{width:1600px;height:800px;border:1px solid black;z-index:-1;position:relative}#main-outer{height:100vh;display:flex;flex-direction:column}#main-inner{height:100%;width:1600px;position:relative;display:block;margin:auto}#cockpit-div{display:flex;width:100%}#top-dash{position:fixed;width:100vw;max-height:100px;min-height:100px}#bottom-dash{position:fixed;width:100vw;bottom:0px}.canvas-image{display:none}
